  transition-duration: 0.1s;
  transition-timing-function: ease-in;
}

.slide-leave-active {
  transition-duration: 0.1s;
  transition-timing-function: cubic-bezier(0, 1, 0.5, 1);
}

.slide-enter-to,
.slide-leave {
  max-height: 100px;
  overflow: hidden;
}

.slide-enter,
.slide-leave-to {
  overflow: hidden;
  max-height: 0;
}

.gx-menu-item {
  // height: 0.625rem;
  line-height: 0.75rem;
}
.gx-menu-item--active > i:first-of-type {
  position: relative;
}
.gx-menu-item--active > i:first-of-type::before {
  position: absolute;
  left: -16px;
  width: 4px;
  height: 100%;
  content: ' ';
  border-left: 4px solid var(--primary);
}
.gx-submenu-item--active > i:first-of-type {
  position: relative;
}
.gx-submenu-item--active > i:first-of-type::before {
  position: absolute;
  left: -32px;
  width: 4px;
  height: 100%;
  content: ' ';
  border-left: 4px solid var(--primary);
}
.gx-submenu-item--active.gx-submenu-item--collapsed > i:first-of-type::before {
  left: -16px;
}
.gx-menu-item--active.gx-is-collapsed {
  // left: -8px;
  // padding-left: 12px;
}
.gx-submenu-item--active {
  color: var(--secondary);
}
